<template>
  <div class="fullscreen_card">

    <eye-new-icon width="300" height="300"></eye-new-icon>
    <div class="main_title">  {{$t("main.header")}}</div> 
    <div class="main_content">  {{$t("main.content")}}</div> 
 
  </div>
</template>

<script>
/* ============
   * Card Component 
   *
   * A basic card component.
   *
   * Gives an idea how components work.
   */

import SlotMixin from "@/mixins/slot";
import eyeIcon from "@/components/icons/eye";
import eyeNewIcon from "@/components/icons/eyeNew";


export default {
  /**
   * The name of the component.
   */
  name: "section",

  /**
   * The mixins that the component can use.
   */
  mixins: [SlotMixin],

  /**
   * The properties that the component accepts.
   */
  props: {
    contextualStyle: {
      type: String,
      required: false
    }
  },
  components: {
    eyeIcon,
    eyeNewIcon
  },
  /**
   * The computed properties that the component can use.
   */
  computed: {
    /**
     * Computed property which will compute the classes
     * for the header of the card.
     *
     * @returns {Array} The classes for the header.
     */
    classNamesHeader() {
      const classNames = ["card-header"];

      if (this.contextualStyle) {
        classNames.push(`bg-${this.contextualStyle}`);
        classNames.push("text-white");
      } else {
        classNames.push("bg-default");
      }

      return classNames;
    }
  }
};
</script>



